.header{
     height: 100vh; 
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative; 
}
img{
    object-fit: cover;
}
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 20rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
@media screen and (max-width: 1024px) {
    .header {
        height: 100vh;
    }

}


/*************** MEDIA QUERIES ( SMALL DEVICES )*********************/

@media screen and (max-width: 600px) {
    .header {
        height: 100vh;
    }
    .header_socials, .scroll_down {
        display: none;
    }
}